<template>
  <div class="hello-login">
    <Menu></Menu>
    <h1>Welcome to My Small House!</h1>
    <div class="myform">
      <el-form :model="userInfo" :rules="rules" label-width="120px" ref="loginForm">

        <el-form-item prop="username" label="用户名:">
          <el-input type="string" v-model="userInfo.username" ></el-input>
        </el-form-item>

        <el-form-item prop="password" label="密码:">
          <el-input v-model="userInfo.password" type="password"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="login">登&ensp;&ensp;录</el-button>
          <span>&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;</span>
          <el-button @click="resetForm('loginForm')">重&ensp;&ensp;置</el-button>
        </el-form-item>
      </el-form>
    </div>
    
  </div>
</template>

<script>
import Menu from '@/components/menu'
export default {
  components: {
    Menu,
  },
  data() {
    var checkUsername = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('用户名不能为空'));
      } else {
        callback();
      }
    };
    var checkPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('密码不能为空'));
      } else {
        callback();
      }
    };
    return {
      userInfo: {
        username: '',
        password: '',
        remember: false
      },
      rules : {
        username : [
          { required: true,validator: checkUsername, trigger: 'blur' }
        ],
        password : [
          { required: true,validator: checkPassword, trigger: 'blur' }
        ]
      }
    };
  },
  created: function () {
    this.$http({
      method:'POST',
      url:'/ajax/ifLogin',
      }).then(function(response){
        if (response.body.state=="true") {
          this.$router.push({path: '/'});
        }
      })
  },
  methods: {
    login: function () {
      this.$http({
        method:'POST',
        url:'/ajax/login',
        params: {
          username: this.userInfo.username,
          password: this.userInfo.password
        }
      }).then(function(response){
        if (response.body.state=="true") {
          this.$router.push({path: '/'});
        } else {
          alert("登录失败：用户名或密码错误！");
          if(confirm('\n没有用户？\n现在注册？')){
            this.$router.push({path: '/Register'});
          } else {
            this.$refs['loginForm'].resetFields();
          }
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  .index {
    background-image: url(../image/login_bg.jpg);
    background-size: cover;
  }
</style>

<style scoped>
  .myform {
    margin-top: 90px;
    width:400px;
    margin-left:440px;
  }
  h1 {
    margin-top: 160px;
    font-weight: normal;
    color: #4DAAEB;
  }
</style>


