<template>
 <div class="hello-menu">

<el-menu class="menu" router="router" :default-active="activeIndex" mode="horizontal" @select="handleSelect">
  <el-menu-item index="/" :route="{ path:'/'}">主页</el-menu-item>
  <el-submenu index = "1">
    <template slot="title">登录</template>
    <el-menu-item index="/Login" :route="{ path:'/Login'}">登录</el-menu-item>
    <el-menu-item index="/Register" :route="{ path:'/Register'}">注册</el-menu-item>
  </el-submenu>
  <el-submenu index = "2">
    <template slot="title">{{user}}</template>
    <el-menu-item index="/Userinfo" :route="{ path:'/Userinfo'}">自选股</el-menu-item>
    <el-menu-item index="/Logout" :route="{ path:'/logout'}">注销</el-menu-item>
  </el-submenu>
  <font style="font-family: 隶书; font-size: 2.5em;">姑苏流白的小站</font>
</el-menu>

<!-- <div class="line"></div> -->

</div>
</template>

<script>
  export default {
    data() {
      return {
        user: '未登录',
        activeIndex: '1',
      };
    },
    created: function () {
      this.$http({
        method:'POST',
        url:'/ajax/ifLogin',
        }).then(function(response){
          if (response.body.state=="true") {
            this.user = response.body.user;
          }
          else {
            this.user = "未登录";
          }
        })
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .menu{
    position:fixed; /* 绝对定位，fixed是相对于浏览器窗口定位。 */
    top:0;
    left:0;
    width:100%;
    height:60px;
    z-index:100;  /*层叠顺序，数值越大就越高。页面滚动的时候就不会被其他内容所遮挡。*/ 
  }
</style>


