<template>
  <div class="userinfo">
    <Menu></Menu>
    <div class="title">
      <font style="font-family: 隶书; font-size: 2em; font-style: normal;">{{user}}的股票</font>
    </div>
    <el-table
      :data="data.slice((currentPage-1)*pagesize, currentPage*pagesize)"
      stripe style="width: 100%">
      <el-table-column prop="name" label="名称" align="center"> </el-table-column>
      <el-table-column prop="industry" label="所属行业" align="center"> </el-table-column>
      <el-table-column prop="area" label="地区" align="center"> </el-table-column>
      <el-table-column prop="pe" label="市盈率" align="center"> </el-table-column>
      <el-table-column prop="outstanding" label="流通股本(亿)" align="center"> </el-table-column>
      <el-table-column prop="totals" label="总股本(亿)" align="center"> </el-table-column>
      <el-table-column prop="totalAssets" label="总资产(万)" align="center"> </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="data.length">
    </el-pagination>

  </div>
</template>

<script>
import Menu from '@/components/menu'
export default {
  name: 'userinfo',
  components: {
    Menu,
  },  
  data () {
    return {
      user: "",
      data: [],
      currentPage:1,
      pagesize:20,
      }
  },
  created: function () {
    this.$http({
      method:'POST',
      url:'/ajax/ifLogin',
      }).then(function(response){
        if (response.body.state=="false") {
          this.$router.push({path: '/login'});
        } else {
          this.user = response.body.user;
        }
      })
  },
  mounted: function () {
    this.$http({
      method:'POST',
      url:'/ajax/userInfo',
      }).then(function(response){
        this.data = response.body.data;
      })
  },
  methods: {
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage){
      this.currentPage = currentPage;
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



