<template>
  <div class="hello-register">
    <Menu></Menu>
    <h1>Welcome to My Small House!</h1>
    <div class="registerform">

      <el-form :model="register" :rules="rules" label-width="100px" ref="registerForm">

        <el-form-item prop="user" label="用户名:">
          <el-input type="text" v-model="register.user" ></el-input>
        </el-form-item>

        <el-form-item prop="psw" label="密码：">
          <el-input v-model="register.psw" type="password"></el-input>
        </el-form-item>

        <el-form-item prop="checkpsw" label="确认密码：">
          <el-input v-model="register.checkpsw" type="password"></el-input>
        </el-form-item>

        <el-form-item prop="mail" label="邮箱：">
            <el-input v-model="register.mail"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('registerForm')" @click="submitform">注&ensp;&ensp;册</el-button>
          <span>&ensp;&ensp;&ensp;&ensp;&ensp;&ensp;</span>
          <el-button @click="resetForm('registerForm')">重&ensp;&ensp;置</el-button>
        </el-form-item>


      </el-form>
    </div>
    
  </div>
</template>


<script>
import Menu from '@/components/menu'
export default {
  components: {
    Menu,
  },
    data() {
      var checkUser = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'));
        }
        setTimeout(() => {
          if (!value) {
            callback(new Error('请输入用户名'));
          } else { callback() }
        }, 2000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.register.checkpsw !== '') {
            this.$refs.register.validateField('checkpsw');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.register.psw) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };

      return {
        register: {
          user: '',
          psw: '',
          checkpsw: '',
          mail: '',
          region: '',
          radio: 3,
          type: [],
          desc: '',
        },
        rules : {
          user : [
            { required: true,validator: checkUser, trigger: 'blur' }
          ],
          psw : [
            { required: true,validator: validatePass, trigger: 'blur' }
          ],
          checkpsw : [
            { required: true,validator: validatePass2, trigger: 'blur' }
          ],
          mail:[
            { required: true, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
          ],
        }
      };
    },

  created: function () {
    this.$http({
      method:'POST',
      url:'/ajax/ifLogin',
      }).then(function(response){
        if (response.body.state=="true") {
          this.$router.push({path: '/'});
        }
      })
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('成功提交!');
        } else {
          console.log('提交失败!!');
          return false;
        }
      });
    },
    submitform: function () {
      this.$http({
        method:'POST',
        url:'/ajax/register',
        params: {
          username: this.register.user,
          password: this.register.checkpsw,
          mail: this.register.mail
        }
      }).then(function(response){
        if (response.body.state=="true") {
          alert("注册成功，现在登录？")
          this.$router.push({path: '/Login'});
        } else {
          alert("登录失败：用户名已经被使用！");
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  .index {
    background-image: url(../image/register_bg.jpg);
    background-size: cover;
  }
</style>

<style scoped>
  .registerform {
    width:400px;
    margin-left:450px;
  }
  h1 {
    font-weight: normal;
  }
</style>


